/* Define the animation */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Apply the animation */
.animate-in {
    animation: fadeInUp 0.5s ease-out;
    animation-delay: calc(var(--index) * 0.13s);
}
  
.glow-border {
    position: relative;
}
  
.glow-border::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: inherit;
    pointer-events: none;
    background: radial-gradient(700px circle at var(--mouse-x, 20%) var(--mouse-y, 20%), rgb(255, 255, 255, 0.07), transparent 30%);
    opacity: 0;
    transition: opacity 200ms;
}

.glow-border:hover::before {
    opacity: 1;
}

/* Adjust the selector as needed */
.relative:hover .absolute {
  display: block;
}

/* Adjust tooltip styles as needed */
