/* Default scrollbar styles */
:root {
  --scrollbar-thumb-color: #a0aec0;
  --scrollbar-track-color: #edf2f7;
}

/* Dark mode scrollbar styles */
@media (prefers-color-scheme: dark) {
  :root {
    --scrollbar-thumb-color: #cbd5e0; /* Dark mode thumb color */
    --scrollbar-track-color: #4a5568; /* Dark mode track color */
  }
}

/* Apply scrollbar styles */
::-webkit-scrollbar {
  width: 10px; /* Set the width of the scrollbar */
}

::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb-color); /* Set the color of the thumb */
  border-radius: 4px; /* Set the border radius of the thumb */
}

::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track-color); /* Set the color of the track */
}

